import React, { useCallback, useMemo } from "react";
import { useHistory } from "react-router-dom";
import { connect } from "react-redux";
import { formatDistance } from "date-fns";
import { Segment, Header, List, Button } from "semantic-ui-react";
import { getDistance } from "geolib";

import { useEvents } from "../hooks";

const EventList = ({ events }) => {
  const history = useHistory();
  const displayEvents = useEvents(events);

  const handleItemClick = useCallback((id) => {
    history.push(`/events/${id}`);
  }, [history]);

  const handleAddEvent = () => {
    history.push("/submit");
  };

  const listItems = useMemo(
    () =>
      displayEvents.map((event, index) => (
        <List.Item key={index} onClick={() => handleItemClick(event.eventId)}>
          <List.Content floated="right">
            {formatDistance(parseInt(event.reportedDt), Date.now(), { addSuffix: true })}
          </List.Content>
          <List.Content floated="left" style={{ paddingTop: 8 }}>
            <i
              className={`ri-${event.icon}-fill ri-xl`}
              style={{ color: event.color }}
            />
          </List.Content>
          <List.Content>
            <List.Header>{event.eventDesc}</List.Header>
            <List.Description>
              <b>{`${getDistance(
                { lat: 40.671613, lon: -73.951909 },
                event.coordinates,
                5
              )}ft`}</b>
            </List.Description>
          </List.Content>
        </List.Item>
      )),
    [displayEvents, handleItemClick]
  );

  return (
    <Segment className="clickable" raised inverted style={{ padding: 16 }}>
      <div className="panel-header">
        <div className="panel-header-text">
          <Header inverted as="h3">
            Nearby events
          </Header>
        </div>
        <div>
          <Button
            size="small"
            inverted
            color="blue"
            circular
            onClick={handleAddEvent}
          >
            <i className="ri-add-fill ri-sm" style={{ paddingRight: 4 }} />
            Add
          </Button>
          <Button size="small" inverted circular content="Filter" />
        </div>
      </div>
      <List inverted divided selection verticalAlign="middle">
        {listItems}
      </List>
    </Segment>
  );
};

const mapStateToProps = (state) => {
  return { events: state.events.nearbyEvents };
};

export default connect(mapStateToProps, {})(EventList);
